<template><undefined>
<h1>Collapse 折叠面板</h1>
<p>可以折叠/展开的内容区域。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>对复杂区域进行分组和隐藏，保持页面的整洁。</li>
<li><code v-pre="">手风琴</code> 是一种特殊的折叠面板，只允许单个内容区域展开。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-collapse @change=\&quot;onChange\&quot; v-model=\&quot;activeIndex\&quot;&gt;\n        &lt;v-panel header=\&quot;This is panel header 1\&quot; index=\&quot;1\&quot;&gt;\n            A dog is a type of domesticated animal. Known for its loyalty and faithfulness, it can be found as a welcome guest in many households across the world.\n        &lt;/v-panel&gt;\n        &lt;v-panel header=\&quot;This is panel header 2\&quot; index=\&quot;2\&quot;&gt;\n            A dog is a type of domesticated animal. Known for its loyalty and faithfulness, it can be found as a welcome guest in many households across the world.\n        &lt;/v-panel&gt;\n        &lt;v-panel header=\&quot;This is panel header 3\&quot; index=\&quot;3\&quot;&gt;\n            A dog is a type of domesticated animal. Known for its loyalty and faithfulness, it can be found as a welcome guest in many households across the world.\n        &lt;/v-panel&gt;\n    &lt;/v-collapse&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        return {\n            activeIndex:['1'],\n        }\n    },\n    methods: {\n        onChange(index, status) {\n            console.log(`第${index}个面板发生变化，目前状态: ${status}`);\n        },\n    },\n    watch: {\n        activeIndex(val){\n            console.log(val);\n        },\n    },\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-collapse @change="onChange" v-model="activeIndex">
        <v-panel header="This is panel header 1" index="1">
            A dog is a type of domesticated animal. Known for its loyalty and faithfulness, it can be found as a welcome guest in many households across the world.
        </v-panel>
        <v-panel header="This is panel header 2" index="2">
            A dog is a type of domesticated animal. Known for its loyalty and faithfulness, it can be found as a welcome guest in many households across the world.
        </v-panel>
        <v-panel header="This is panel header 3" index="3">
            A dog is a type of domesticated animal. Known for its loyalty and faithfulness, it can be found as a welcome guest in many households across the world.
        </v-panel>
    </v-collapse>
</template>

</div>
                  <div slot="description"><h4 id="zhe-die-mian-ban"><a class="header-anchor" href="#zhe-die-mian-ban" aria-hidden="true">¶</a> 折叠面板</h4>
<p>可以同时展开多个面板，这个例子默认展开了第一个。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-collapse</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onChange"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"activeIndex"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-panel</span> <span class="hljs-attr">header</span>=<span class="hljs-string">"This is panel header 1"</span> <span class="hljs-attr">index</span>=<span class="hljs-string">"1"</span>&gt;</span>
            A dog is a type of domesticated animal. Known for its loyalty and faithfulness, it can be found as a welcome guest in many households across the world.
        <span class="hljs-tag">&lt;/<span class="hljs-name">v-panel</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-panel</span> <span class="hljs-attr">header</span>=<span class="hljs-string">"This is panel header 2"</span> <span class="hljs-attr">index</span>=<span class="hljs-string">"2"</span>&gt;</span>
            A dog is a type of domesticated animal. Known for its loyalty and faithfulness, it can be found as a welcome guest in many households across the world.
        <span class="hljs-tag">&lt;/<span class="hljs-name">v-panel</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-panel</span> <span class="hljs-attr">header</span>=<span class="hljs-string">"This is panel header 3"</span> <span class="hljs-attr">index</span>=<span class="hljs-string">"3"</span>&gt;</span>
            A dog is a type of domesticated animal. Known for its loyalty and faithfulness, it can be found as a welcome guest in many households across the world.
        <span class="hljs-tag">&lt;/<span class="hljs-name">v-panel</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-collapse</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">activeIndex</span>:[<span class="hljs-string">'1'</span>],
        }
    },
    <span class="hljs-attr">methods</span>: {
        onChange(index, status) {
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">`第<span class="hljs-subst">${index}</span>个面板发生变化，目前状态: <span class="hljs-subst">${status}</span>`</span>);
        },
    },
    <span class="hljs-attr">watch</span>: {
        activeIndex(val){
            <span class="hljs-built_in">console</span>.log(val);
        },
    },
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
