<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="collapse-props"><a class="header-anchor" href="#collapse-props" aria-hidden="true">¶</a> Collapse Props</h3>
<table class="table"><thead>
<tr>
<th>成员</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>activeIndex</td>
<td>当前展开状态，可用v-model</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>accordion</td>
<td>开启手风琴模式</td>
<td>Boolean</td>
