<script>
export default {
    data() {
        return {
            activeIndex: ['1'],
            accordionActiveIndex: ['1','2'],
            activeIndexMore:['1'],
            activeIndexSimple:['1'],
            customPanelStyle: {
                background: '#f7f7f7',
                borderRadius: '4px',
                marginBottom: '24px',
                border: 0,
            },
        }
    },
    methods: {
        onChange(index, status) {
            console.log(`第${index}个面板发生变化，目前状态: ${status}`);
        },
    },
    watch: {
        activeIndex(val){
            console.log(val);
        },
        accordionActiveIndex(val){
            console.log(val);
        },
    },
}
</script>


